import React, { useState } from 'react';
import BookingDetails from './BookingDetails';
import SearchBar from './SearchBar';

const Bookings = () => {
  const initialBookings = [
    {
      id: 1,
      name: 'Sharook',
      mobileNo: '9995990001',
      itemName: 'Yamaha R15',
      itemNo:'KL11AS6606',
      startDate: '2024-06-15',
      endDate: '2024-06-15',
      bookingTime: '10:00 AM',
      tripStatus: 'Booked',
      advance: 100,
      balance: 900
    },
    {
      id: 2,
      name: 'Ajmal',
      mobileNo: '8078254516',
      itemName: 'Unicorn',
      itemNo: 'KL11AK1126',
      startDate: '2024-06-26',
      endDate: '2024-06-26',
      bookingTime: '10:00 AM',
      tripStatus: 'Booked',
      advance: 200,
      balance: 700
    },
    {
      id: 3,
      name: 'Nibras',
      mobileNo: '8078254513',
      itemName: 'Himalayan',
      itemNo: 'KL85AG1126',
      startDate: '2024-06-24',
      endDate: '2024-06-26',
      bookingTime: '11:00 AM',
      tripStatus: 'Booked',
      advance: 200,
      balance: 1000
    }
  ];
  const [bookings, setBookings] = useState(initialBookings);

  const [searchQuery, setSearchQuery] = useState('');

  const handleSearchChange = (event) => {
    setSearchQuery(event.target.value);
  };

  const filteredBookings = bookings.filter(booking => 
    booking.itemNo.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const markAsCompleted = (id) => {
    const isConfirmed = window.confirm('Confirm completion of this trip');

  // If user confirmed, proceed to remove the booking
  if (isConfirmed) {
    setBookings(bookings.filter(booking => booking.id !== id));
    // Optionally, make an API call to update the server
    // axios.put(`/api/bookings/${id}`, { status: 'completed' });
  }
  };

  
  return(
  <div>
    <SearchBar value={searchQuery} onChange={handleSearchChange} />
    {filteredBookings.map((booking) => (
      <BookingDetails key={booking.id} booking={booking} markAsCompleted={markAsCompleted}/>
    ))}
  </div>
  );
  
};

export default Bookings;
