import React, { useState } from 'react';
import DatePicker from 'react-datepicker';
import 'react-datepicker/dist/react-datepicker.css';
import styles from './Available.module.css';

const Available = () => {
  const [bikes, setBikes] = useState([
    { "id": "KL11AS6636", name: "Unicorn", unavailableDates: [] },
    { "id": "KL85B9607", name: "Himalayan", unavailableDates: [] },
    // Add more bikes as needed
  ]);
  
  const [startDate, setStartDate] = useState(new Date());

  const toggleBlockBike = (bikeId) => {
    setBikes((prevBikes) =>
      prevBikes.map((bike) =>
        bike.id === bikeId ? { ...bike, blocked: !bike.blocked } : bike
      )
    );
  };

  return (
    <div className={styles.container}>
      <h1 className={styles.header}>Available Bikes</h1>
      <div className={styles['date-picker-container']}>
        <label className={styles.label}>Select Date:</label>
        <DatePicker
          selected={startDate}
          onChange={(date) => setStartDate(date)}
          dateFormat="yyyy/MM/dd"
          className={styles['date-picker']}
        />
      </div>
      
      <div className={styles['card-container']}>
        {bikes.map((bike) => (
          <div
            key={bike.id}
            className={`${styles.card} ${bike.blocked ? styles.blocked : ''}`}
          >
            <div><strong>{bike.name} - #{bike.id}</strong></div>
            <button
              className={`${styles['block-button']} ${bike.blocked ? styles['unblock-button'] : ''}`}
              onClick={() => toggleBlockBike(bike.id)}
            >
              {bike.blocked ? 'Unblock' : 'Block'}
            </button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Available;
