// src/components/Home.js
import React, { useState } from 'react';
import Card from './Card';
import Modal from './Modal';
import './Home.css';

const Home = () => {
  const initialBikes = [
    {
      id: 1,
      bike: 'Yamaha R15',
      bikeNo: 'MH12AB1234',
      pricePerDay: '500',
      pricePerKm: '5',
      freeKmPerDay: '100',
      location: 'Pune'
    }
  ];

  const [bikes, setBikes] = useState(initialBikes);
  const [showModal, setShowModal] = useState(false);
  const [formData, setFormData] = useState({
    id: null,
    bike: '',
    bikeNo: '',
    pricePerDay: '',
    pricePerKm: '',
    freeKmPerDay: '',
    location: ''
  });

  const handleShowModal = () => {
    setFormData({
      id: bikes.length + 1,
      bike: '',
      bikeNo: '',
      pricePerDay: '',
      pricePerKm: '',
      freeKmPerDay: '',
      location: ''
    });
    setShowModal(true);
  };

  const handleCloseModal = () => setShowModal(false);

  const handleChange = (e) => {
    setFormData({
      ...formData,
      [e.target.name]: e.target.value
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setBikes([...bikes, formData]);
    handleCloseModal();
  };

  const deleteBike = (id) => {
    const updatedBikes = bikes.filter((bike) => bike.id !== id);
    setBikes(updatedBikes);
  };

  const updateBike = (updatedBike) => {
    const updatedBikes = bikes.map((bike) => (bike.id === updatedBike.id ? updatedBike : bike));
    setBikes(updatedBikes);
    // Placeholder for API call
    // axios.put(`/api/bikes/${updatedBike.id}`, updatedBike);
  };

  return (
    <div className="home">
      <div className="card-container">
        {bikes.map((bike) => (
          <Card key={bike.id} bike={bike} onDelete={deleteBike} onUpdate={updateBike} />
        ))}
      </div>
      <button className="add-button" onClick={handleShowModal}>+</button>
      <Modal
        show={showModal}
        handleClose={handleCloseModal}
        handleSubmit={handleSubmit}
        handleChange={handleChange}
        formData={formData}
      />
    </div>
  );
};

export default Home;
